<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure class="section-masthead__media">
				<lazy-image :src="imgSrc" :alt="imgAlt"/>
			</figure>
			<section class="section-masthead__text">
				<h1>Yamaha Motors Europe</h1>
				<p class="subtitle">Conquering the heart of riders and the respect of motorbike customizers</p>
			</section>
		</header>
		<article class="work">
			<section class="work__detail">
				<h3 class="work__subtitle">Context</h3>
				<p>
					Sport Heritage is Yamaha Motors segment that combines retro looking style with modern performance.
				</p>
				<p>
					The brand had some historic models in the category but never fully capitalized on the entire range
					before.
				</p>
				<p>
					In addition modern riders were increasingly going directly to customizers with their bikes, rather
					than buying “ready to go” custom models.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Insight</h3>
				<p>
					The strategy and insight at the basis of this campaign was based on a unique “bikestorming” that
					took place in Amsterdam with Client, Agency and a selection of some of the best customizers in the
					world.
				
				</p>
				<p>
					This inclusive approach helped us gain the respect of the custom community, as they realized YME
					wanted to create something unique together.
				
				</p>
				<p>
					The “bikestorming” helped us unlock the insight that today’s riders were looking for unique,
					retro-looking designs but were not willing to compromize on performance.
				
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Creative idea</h3>
				<p>
					Faster Sons is the creative platform that will represent all products in this category.
				
				</p>
				<p>
					All Yamaha's Sport Heritage motorcycles are built with respect to “Fathers”, the legendary Yamaha
					bikes from the past, but built without compromise on riding capability”.
				
				</p>
				<p>
					Rather than creating just print or riding videos wedeveloped a set of long-form videos showcasing
					the approach that every customizer took when working on their models. 3 Champters of the Faster Sons
					story were produced, each bringing motor enthusiast into the world of top-level cusomization.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Impact</h3>
				<p>
					Thanks to the impact of SH sales in the market as well as that of other segments, Yamaha Motors
					gained leadership of the European market in 2017. XSR700, the hero model of this segment was among
					the top-ten of best selling bikes in Europe.
				
				</p>
			</section>
		</article>
		<video-wrapper src="//www.youtube.com/embed/LADLAhA7hkM"
		               title="Bayer - Aspirin C"/>
		<app-footer/>
	</section>
</template>

<script>

	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';

	export default {
		name: 'Yamaha',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__media {
		height: 100%;
		position: relative;
	}
	
	.section-masthead__media::after {
		content: '';
		position: absolute;
		top: -7px;
		left: 7px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		font-size: 5vw;
	}
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -65%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



